.home-container {
    /* padding: 0; */
    /* margin: 0; */
    height: 100%;
    position: relative;
    overflow: hidden;
    /* background: linear-gradient(rgb(114, 175, 235), #badfff); */
}
.snowflake {
    --size: 1vw;
    width: var(--size);
    height: var(--size);
    background: white;
    border-radius: 50%;
    position: absolute;
    top: -5vh;
}
@keyframes snowfall {
    0% {
        transform: translate3d(var(--left-ini), 0, 0);
   }
    100% {
        transform: translate3d(var(--left-end), 110vh, 0);
   }
}
.snowflake:nth-child(1) {
    --size: 1vw;
    --left-ini: -8vw;
    --left-end: 3vw;
    left: 21vw;
    animation: snowfall 12s linear infinite;
    animation-delay: -10s;
}
