.home-content>h1{
    font-family: 'LOTTERIACHAB';
    font-weight: bold;
    font-size: 80px;
    color:white;
    text-align: center;
    position: absolute;
    top: 100px;
    text-shadow: -5px 0px rgba(25, 83, 176, 0.659), 0px 5px rgba(25, 83, 176, 0.659), 5px 0px rgba(25, 83, 176, 0.659), 0px -5px rgba(25, 83, 176, 0.659);
    margin-top: -80px;
    white-space: nowrap;
}

.home-content{
    width: 1500px;
    height: 100vh;
    margin: auto;
    display: flex;
    justify-content: center;
    align-items: center;
}
.rabbit{
    width: 250px;
    transition: .4s;
}
.rabbit:hover{
    scale: 1.2;
    cursor: pointer;
}

.rabbit:nth-child(1):hover .comment1{
    display: none;
}

.comment{
    padding-top: 100px;
    font-family: 'montserrat';
    font-size: 30px;
    font-weight: bold;
    color: white;
    white-space: nowrap;
    text-align: center;
}
.hide-comment{
    display:none;
}

@font-face {
    font-family: 'LOTTERIACHAB';
    src: url('https://cdn.jsdelivr.net/gh/projectnoonnu/noonfonts_2302@1.0/LOTTERIACHAB.woff2') format('woff2');
    font-weight: normal;
    font-style: normal;
}